.my-program {
  padding-top: 40px;

  &__title {}

  &__link {
    display: flex;
    padding: 40px;
    background: #FFFFFF;
    border: 1px solid #E0E3EE;
    border-radius: 10px;
    margin-bottom: 20px;

    @include mw(1440) {
      padding-left: 15px;
      padding-right: 15px; } }

  &__img-wrap {
    width: 220px;
    height: 220px;
    flex-shrink: 0;

    @include mw(768) {
      width: 28%;
      height: auto;
      margin-bottom: 20px; } }

  &__img {

    @include mw(768); }

  &__content {
    flex-grow: 1;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;

    @include mw(1440) {
      margin-left: 20px; }

    @include mw(768) {
      margin-left: 0;
      flex-direction: row;
      flex-wrap: wrap; } }

  &__stat {
    justify-content: space-between; } }

.office-container {
  max-width: 1212px;
  @include padding-left(4rem);
  @include padding-right(4rem);

  @include mw(1440) {
    padding-left: 15px;
    padding-right: 15px; } }

.office-title {
  font-weight: 600;
  @include font-size(1.5rem);
  margin-bottom: 40px; }

.my-program {
  margin-bottom: 100px;

  @include mw(768) {
    margin-bottom: 60px; }

  & .pay-content {

    &__title {
      color: $text;

      @include mw(768) {
        margin-left: auto;
        margin-right: auto;
        align-self: center;
        margin-bottom: 0;
        transform: translateY(-10px); } }

    &__text {
      color: $text; }

    &__moduls {
      color: $text;
      margin-bottom: 0;
      max-width: 400px;

      @include mw(768) {
        max-width: none;
        margin-bottom: 20px; } }

    &__inner {
      display: flex;
      margin-top: auto;
      align-items: center;
      justify-content: space-between;

      @include mw(768) {
        flex-direction: column;
        align-items: flex-start; } }

    &__moduls-block {}

    &__moduls-exercise {}

    &__bonus {
      max-width: 250px;

      @include mw(1440) {
        margin-left: 15px; }

      @include mw(768) {
        max-width: none;
        margin-left: 0; } } } }

.size-1 {}

.stat {
  display: flex;
  padding: 30px;
  align-items: center;
  background-color: #fff;
  border: 1px solid #E0E3EE;
  border-radius: 10px;

  &--gorizontal {
    background-image: url(/img/stat-bg.png);
    background-repeat: no-repeat;
    background-position: 30% bottom;

    @include mw(768) {
      flex-direction: column;
      align-items: flex-start;
      background-image: url(/img/stat-bg-big.png);
      padding-bottom: 120px;
      padding-left: 15px;
      padding-right: 15px;
      background-position: 50% bottom; } }

  &__title {
    font-weight: 600;
    text-align: center;

    @include mw(768) {
      margin-bottom: 30px;
      text-align: left; } }

  &__list {
    display: flex;
    align-items: center;

    &--gorizontal {
      justify-content: space-between;
      flex-grow: 1;
      max-width: 590px;
      margin-left: 20px;

      @include mw(768) {
        margin-left: 0;
        flex-direction: column;
        align-items: flex-start; } } }

  &__item {
    display: flex;
    flex-direction: column;

    @include mw(768) {

      & + li {
        margin-top: 30px; } } }

  &__term {
    font-size: 14px;
    margin-bottom: 5px;

    @include mw(768) {
      font-size: 18px; } }

  &__def {
    font-weight: 600; }

  &__def--time {} }
