.steps {
  margin-top: 100px;
  margin-bottom: 50px;

  @include mw(768) {
    margin-top: 0;
    margin-bottom: 40px; }

  &__title {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 40px;

    @include mw(768) {
      margin-bottom: 20px; }

    & mark {
      background-color: transparent;
      background-image: linear-gradient(rgba(#7117E4, 1), rgba(#7117E4, 0.3));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } }

  &__list {
    display: flex;
    justify-content: space-between;

    @include mw(768) {
      max-width: 300px;
      margin-left: auto;
      margin-right: auto; } }

  & .slick-arrow {
    width: 40px;
    height: 40px;
    background-image: url(/img/svg/arr-slider.svg);
    background-repeat: no-repeat;
    background-size: 14px 20px;
    background-position: center center;

    &::before {
      content: none; } }

  & .slick-next {
    right: -15px; }

  & .slick-prev {
    left: -15px;
    transform: rotate(180deg) translateY(20px);
    z-index: 1; } }

.steps-card {
  width: calc(25% - (60px / 4));
  background: #FFFFFF;
  box-shadow: -10px 10px 20px rgba(145, 145, 145, 0.1);
  border-radius: 10px;
  padding: 40px;

  @include mw(768) {
    min-height: 354px;
    width: 100%;
    box-shadow: none; }

  &__img-wrap {
    width: 100px;
    height: 112px;
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-size: contain;
    margin-bottom: 25px;

    @include mw(768) {
      margin-left: auto;
      margin-right: auto; } }

  &__img-wrap--reg {
    background-image: url(/img/reg.png); }

  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #46404C;
    margin-bottom: 20px;

    @include mw(768) {
      text-align: center; } }

  &__text {
    font-size: 16px;
    line-height: 20px;
    color: #46404C;

    @include mw(768) {
      text-align: center; } }

  &__img-wrap--education {
    background-image: url(/img/education.png); }

  &__img-wrap--power {
    background-image: url(/img/power.png); }

  &__img-wrap--profit {
    background-image: url(/img/profit.png); } }

