.calc {
  margin-bottom: 60px;

  @include mw(768) {
    margin-top: 60px;
    margin-bottom: 20px; }

  &__inner {
    display: flex;
    align-items: center; }

  &__left {
    width: 100%;
    max-width: 670px; }

  &__title {
    margin-bottom: 60px;

    & mark {
      background-color: transparent;
      background-image: linear-gradient(rgba(#7117E4, 1), rgba(#7117E4, 0.3));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } }

  &__box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;

    @include mw(600) {
      flex-direction: column;
      margin-bottom: 60px; } }

  &__label {
    width: calc( 50% - 15px );
    display: flex;
    flex-direction: column;
    position: relative;

    @include mw(600) {
      width: 100%;

      & + label {
        margin-top: 40px; } } }

  &__label-text {
    font-size: 16px;
    line-height: 22px;
    color: #8D8D93;
    margin-bottom: 8px; }

  &__range {
    font-size: 18px;
    line-height: 24px;
    color: #131213;
    padding: 21px 22px;
    background-color: #F0F0F4;
    border: 1px solid #E6E6EA;
    border-radius: 4px 4px 4px 0; }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;

    @include mw(600) {
      flex-direction: column;
      margin-bottom: 60px; } }

  &__item {

    @include mw(600) {
      display: flex;
      justify-content: space-between;
      align-items: center;

      & + li {
        margin-top: 30px; } } }

  &__term {
    font-size: 18px;
    line-height: 24px;
    color: #131213;
    margin-bottom: 10px; }

  &__description {
    font-weight: 600;
    font-size: 42px;
    line-height: 48px;
    color: #131213;

    @include mw(768) {
      font-size: 36px; } }

  &__link {

    &--desctop {
      @include mw(768) {
        display: none; } }

    &--tablet {
      display: none;

      @include mw(768) {
        display: block; } } }

  &__right {
    max-width: 397px;
    width: 30%;
    margin-left: auto;
    margin-right: auto;

    @include mw(768) {
      display: none; } }

  &__img-wrap {
    height: 0;
    width: 100%;
    padding-top: 145%;
    background-image: url(/img/calc-bg.png);
    background-size: contain; } }

.irs {
  display: block;
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;

  &-single {
    position: absolute; }

  &-min {
    display: none; }

  &-max {
    display: none; }

  &-single {
    width: 20px;
    height: 20px;
    font-size: 0;
    background: #FFFFFF;
    border: 2px solid #903EF9;
    border-radius: 50%;
    z-index: 1; }

  &-grid {
    display: none; }

  &-bar {
    position: absolute;
    height: 2px;
    bottom: 3px;
    background-color: #903EF9; } }

.deposit {
  padding-top: 250px;
  background-image: url(/img/deposit-left.png), url(/img/deposit-right.png);
  background-size: 24% auto;
  background-position: 0 6%, 100% 15%;
  background-repeat: no-repeat;
  margin-bottom: 60px;

  @include mw(1000) {
    background-image: none;
    padding-top: 70px; }

  &__title {
    text-align: center;
    margin-bottom: 43px;

    & mark {
      background-color: transparent;
      background-image: linear-gradient(rgba(#7117E4, 1), rgba(#7117E4, 0.3));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } }

  &__subtitle {
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #46404C;
    max-width: 433px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 57px; }

  &__list {
    display: flex;
    justify-content: space-between;

    @include mw(768) {
      width: 100%;
      flex-direction: column; } }

  &__item {
    width: calc((100% / 3) - (40px / 3));
    box-shadow: -10px 10px 20px rgba(144, 144, 144, 0.1);
    border-radius: 10px;
    overflow: hidden;

    @include mw(768) {
      box-shadow: none;
      width: 100%;

      & + li {
        margin-top: 30px; } } } }

.deposit-card {
