.farm {
  padding-top: 40px;

  &__inner {
    display: flex;
    justify-content: space-between;

    @include mw(768) {
      flex-direction: column;
      align-items: center; } }

  &__col--left {
    width: 100%;
    max-width: 680px;

    @include mw(768) {
      margin-bottom: 30px; } }

  &__subtitle {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #47404C;
    margin-bottom: 21px; }

  &__col--right {
    width: 100%;
    max-width: 340px;
    margin-left: 20px;
    flex-shrink: 1;

    @include mw(768) {
      margin-left: 0; }

    @include mw(600) {
      max-width: none; } } }

.miners {
  background: #FFFFFF;
  border-radius: 10px;
  padding: 40px 40px 54px 40px;
  margin-bottom: 40px;

  @include mw(768) {
    padding-left: 15px;
    padding-right: 15px; }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;

    @include mw(768) {
      display: block;
      max-width: 100%; } }

  &__link {
    display: block;
    transition: 0.2s;

    &:hover {
      opacity: 0.8; } }

  &__img-wrap {
    min-height: 170px;
    background-color: #F8F9FC;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: 50% 0;

    &::after {
      content: "";
      background-image: url(/img/plus.png);
      width: 40px;
      height: 40px;
      display: block;
      background-repeat: no-repeat;
      background-size: contain; } }

  &__img-wrap--active {
    background-image: url(/img/miners-bg-active.png);
    background-size: contain;

    &::after {
      content: none; } }

  &__text {
    font-weight: 600;
    font-size: 16px;
    line-height: 18px;
    color: #46404C;
    padding: 15px 20px; }

  &__link-sell {
    display: none;

    @include mw(768) {
      display: block;
      margin-top: 30px;
      text-align: center; } }

  &__item {
    box-shadow: 5px 5px 10px #F8F9FC;
    border-radius: 4px;
    width: calc((100% / 3) - 20px);
    margin-bottom: 30px;
    border: 1px solid #E6E6EA;
    overflow: hidden;

    @include mw(768) {
      margin-bottom: 0; } }

  & .slick-arrow {
    width: 40px;
    height: 40px;
    background-image: url(/img/svg/arr-slider.svg);
    background-repeat: no-repeat;
    background-size: 14px 20px;
    background-position: center center;

    &::before {
      content: none; } }

  & .slick-next {
    right: 0; }

  & .slick-prev {
    left: 0;
    transform: rotate(180deg) translateY(20px);
    z-index: 1; } }

.farm-profit {
  width: 100%;
  background-color: #fff;
  border-radius: 10px;
  padding: 40px;

  @include mw(768) {
    padding-left: 15px;
    padding-right: 15px; }

  &__title {
    margin-bottom: 50px; }

  &__chart {
    height: 370px;
    width: 100%;
    margin-bottom: 40px;

    @include mw(600) {
      height: 300px; } }

  &__box {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mw(768) {
      flex-direction: column; } }

  &__profit {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #47404C;
    display: flex;
    align-items: center;

    @include mw(768) {
      margin-bottom: 30px; } }

  &__sum {
    margin-left: 20px; }

  &__proc {
    margin-left: 14px; }

  &__link {

    @include mw(600) {
      display: block;
      width: 100%;
      text-align: center; } } }

.proc {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  padding-left: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-right: 5px;
  border-radius: 2px;
  display: flex;
  align-items: center;

  &::before {
    width: 8px;
    height: 8px;
    display: block;
    margin-right: 3px;
    content: ""; } }

.proc--plus {
  background-color: #D7FABF;
  color: #4AAF05;

  &::before {
    background-image: url(/img/svg/arrow-up.svg); } }

.link-sell {
  font-weight: 500;
  font-size: 16px;
  color: #47404C;
  border: 1px solid #E0E3EE;
  border-radius: 4px;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 37px;
  padding-right: 37px; }

.rouble-mark {
  display: flex;
  align-items: center; }

.rouble-mark {

  &--stat {

    & svg {
      margin-left: 3px;
      height: 14px;
      width: 12px; } }
  &--profit {

    & svg {
      margin-left: 3px;
      height: 14px;
      width: 12px;
      transform: scale(1.2) translateY(1px); } } }

.stat {

  &__box {
    display: flex;
    align-items: center; }

  &__proc {
    margin-left: 10px; } }

.proc--minus {
  background-color: #FFD1D7;
  color: #FF5669;

  &::before {
    background-image: url(/img/svg/arrow-down.svg); } }

* {
